@import '../abstracts/_abstracts';
div.question-box {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    margin: .3rem 0;
    h1.question {
        font-size: 1.8rem;
        color: $blue;
        font-weight: 400;
        flex-basis: 80%; }
    button.help {
        flex-basis: 5%;
        height: 80%;
        @include btn;
        font-family: 'Poppins', sans-serif;
        color: whitesmoke;
        font-weight: 600; }
    span.tip {
        font-size: 1rem; } }

div.popover-body {
    font-size: 1.3rem; }

div.question-divider {
    padding-bottom: 1.3rem; }


div.progress {
    display: flex;
    border-radius: 5rem;
    height: 2.4rem;
    box-shadow: 0 0 .3rem black;
    margin-bottom: 4rem;
    margin-top: 4rem;
    label {
        text-align: center;
        font-weight: bolder;
        font-size: 1.5rem;
        margin: auto; }
    .progress-bar {
        background-color: $blue; } }

.MuiFormLabel-root {
    font-size: 1.7rem !important;
    color: $blue; }
input.MuiInputBase-input {
    font-size: 2rem;
    color: $blue !important; }
div.MuiFormControl-root {
    width: 40vw;
    color: $blue !important; }
